<template>
    <div class="demo-block">
        <in-table>
            <thead>
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th width="200"></th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in list">
                    <td>{{item.nickName}}</td>
                    <td>{{item.plat}}</td>
                    <td>{{item.code}}</td>
                    <td>{{item.status}}</td>
                    <td>
                        <in-button type="primary" @click="passClick"></in-button>
                        <in-button @click="refuseClick"></in-button>
                    </td>
                </tr>
            </tbody>
        </in-table>
        <div class="pagination">
            <in-pagination :total="50"></in-pagination>
        </div>
        
    </div>
</template>
<script>
export default {
    data(){
        return {
            list:[{
                nickName:'',
                plat:'',
                code:'INGVIP0',
                status:''
            },{
                nickName:'',
                plat:'',
                code:'INGVIP0',
                status:''
            },{
                nickName:'',
                plat:'',
                code:'INGVIP0',
                status:''
            },{
                nickName:'',
                plat:'',
                code:'INGVIP0',
                status:''
            },{
                nickName:'',
                plat:'',
                code:'INGVIP0',
                status:''
            },{
                nickName:'',
                plat:'',
                code:'INGVIP9',
                status:''
            }]
        }
    },
    methods:{
        passClick(){
            this.$message("");
        },
        refuseClick(){
            this.$confirm({
                title:'',
                text:'',
                confirmButton:'',
                cancelButton:'',
                action:(res)=>{
                    if(res == 'confirm'){
                        this.$message("");
                    }else{
                        return false;
                    }
                }
            })
        }
    }
}
</script>
<style lang="less" scoped>
.pagination{
    margin-top: 30px;
    text-align: center;
}
</style>





